* {
	box-sizing: border-box;
}
body {
    font-family: 'Mulish', sans-serif;
    font-size: 16px;
    color: #506172;
    font-weight: 400;
    line-height: 1.5;
    background-color: #F9F9FF;
}
.container-fluid {
	max-width: 100%;
	margin:0 auto;
    padding-left: 15px;
    padding-right: 15px;
    max-width: 1280px;
}
a {
    text-decoration: none;
}
img {
    display: block;
    width: 100%;
}
h1, h2, h3, h4, h5, h6 {
    font-weight: 900;
    color: #152035;
}
h3 {
    font-size: 28px;
}
h4 {
    font-size: 24px;
}
h5 {
    font-size: 20px;
}
h6 {
    font-size: 16px;
}
.btn {
    background-color: #f67280;
    color: #ffffff;
    font-size: 16px;
    font-family: 'Mulish', sans-serif;
    font-weight: 600;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    border-radius: 50%;
}
.show {
    display: block !important;
}
.hide {
    display: none !important;
}
.visible {
    opacity: 1;
    visibility: visible;
}
.invisible {
    opacity: 0;
    visibility: hidden;
}

